<script>
	export default {
		data() {
			return {
				ishow: false,
				is_lia_show: false,
				screenWidth: document.documentElement.clientHeight,
				content: '',
				limttle: ['俩面盘', '数字盘', '一字定位', '二字定位', '三字定位', '一字组合', '二字组合', '二字和数', '组选三', '组选六', '跨度'],
				indexs: 0, //俩面盘点击效果
				dsz:0
			}
		},
		props: {
			endTime: {
				type: String,
				default: '1515400000'
			},
			endText: {
				type: String,
				default: '00 : 00 : 00'
			}
		},
		methods: {
			kuaij() {
				$('.j-odds').hide();
				for(var i = 0; i < $('.palr th').length; i++) {
					if($('.palr th').eq(i).html() == '赔率') {
						$('.palr th').eq(i).attr('colspan', '2')
					}
				}
			},
			yiban() {
				$('.j-odds').show();
				for(var i = 0; i < $('.palr th').length; i++) {
					if($('.palr th').eq(i).html() == '赔率') {
						$('.palr th').eq(i).attr('colspan', ' ')
					}
				}
				
				for(var i = 0; i < $('.palr th').length; i++) {
						$('.palr th').eq(i).attr('colspan', ' ')
				}

				for(var i = 0; i < $('.b-b-c th').length; i++) {
					if($('.b-b-c th').eq(i).html() == '赔率') {
						$('.b-b-c th').eq(i).attr('colspan', ' ')
					}
				}
				$('.j-betting tr').css('background','rgba(0, 0, 0, 0)');
				this.dsz = 0;
			},
			lianm(index) {
				this.indexs = index;
				$('.j-betting tr').css('background','rgba(0, 0, 0, 0)');
				this.dsz = 0;
				console.log(this.indexs)
			},
			countdowm(timestamp) {
				let self = this;
				let timer = setInterval(function() {
					let nowTime = new Date();
					let endTime = new Date(timestamp * 1000);
					let t = endTime.getTime() - nowTime.getTime();
					if(t > 0) {
						let day = Math.floor(t / 86400000);
						let hour = Math.floor((t / 3600000) % 24);
						let min = Math.floor((t / 60000) % 60);
						let sec = Math.floor((t / 1000) % 60);
						hour = hour < 10 ? "0" + hour : hour;
						min = min < 10 ? "0" + min : min;
						sec = sec < 10 ? "0" + sec : sec;
						let format = '';
						if(day > 0) {
							format = `${day} : ${hour} : ${min} : ${sec}`;
						}
						if(day <= 0 && hour > 0) {
							format = `${hour} : ${min} : ${sec}`;
						}
						if(day <= 0 && hour <= 0) {
							format = `${min} : ${sec}`;
						}
						self.content = format;
					} else {
						clearInterval(timer);
						self.content = self.endText;
					}
				}, 1000);
			}
		},
		mounted: function() {
			let that = this;
			window.onresize = function() {
				that.screenWidth = document.documentElement.clientHeight;
			}
			$('.j-betting tr').click(function(){
				console.log($(this).children());
				if($(this).children()[0].nodeName != "TH" && $(this).children().eq(2).css('display') == 'none'){
					if($(this).css('background-color') == 'rgba(0, 0, 0, 0)'){
					 	 $(this).css('background','rgb(255,245,178)');
					 	that.dsz++
					 }else{
					 	 $(this).css('background','rgba(0, 0, 0, 0)');
					 	that.dsz--
					 }
				}
			})
			this.countdowm(this.endTime);

			$('#ffssc_qiuone a').mouseover(function() {
				var index = $(this).index();
				$('#ffssc_qiuone a').removeClass('active')
				$(this).addClass('active');

				$('#ffssc_lhide .tab-bd').hide();
				$('#ffssc_lhide .tab-bd').eq(index).show();
			})

			$('.sub-tabs a').click(function() {
				var index = $(this).index();
				$('.sub-tabs a').removeClass('current');
				$(this).addClass('current');
				$('#j-yzdw .nested-table').hide();
				$('#j-yzdw .nested-table').eq(index).show();
				
				$('#j-zxs .table').hide();
				$('#j-zxs .table').eq(index).show();
				
				$('#j-zxl .table').hide();
				$('#j-zxl .table').eq(index).show();
			})

		}

	}
</script>
